<template>
  <div class="testone-class">
    <div>one</div>
    <div style="margin: 20px">
      <van-button
        round
        block
        type="primary"
        style="margin-top: 10px"
        native-type="submit"
        @click="handlerH5NextPage"
      >h5 --> two</van-button>

      <van-button
        type="info"
        round
        block
        style="margin-top: 10px"
        native-type="submit"
        @click="handlerNaviteNextPage"
      >navive-->two</van-button>

      <van-button
        type="warning"
        round
        block
        style="margin-top: 10px"
        native-type="submit"
        @click="handlerShowNav"
      >显示导航条</van-button>

      <van-button
        type="warning"
        round
        block
        style="margin-top: 10px"
        native-type="submit"
        @click="handlerSetNavTitle"
      >设置导航条文字</van-button>

      <van-button
        type="warning"
        round
        block
        style="margin-top: 10px"
        native-type="submit"
        @click="handlerHiddenNav"
      >隐藏导航条</van-button>
    </div>
  </div>
</template>

<script>
import nativeNav from "@zkty-team/x-engine-module-nav"
import nativeRouter from "@zkty-team/x-engine-module-router"
export default {
  data() {
    return {}
  },
  methods: {
    // 显示原生导航条
    handlerShowNav() {
      nativeNav.setNavBarHidden({
        isHidden: false,
        isAnimation: true,
      })
    },
    // 设置导航条文字
    handlerSetNavTitle() {
      nativeNav.setNavTitle({
        title: "测试文字",
        titleColor: "#DB7093", // 默认黑色
        titleSize: 16, // 默认16
      })
    },
    // 隐藏原生导航条
    handlerHiddenNav() {
      nativeNav.setNavBarHidden({
        isHidden: true,
        isAnimation: true,
      })
    },
    handlerH5NextPage() {
      this.$router.push({
        path: "/testtwo",
      })
    },
    handlerNaviteNextPage() {
      // nativeNav.navigatorPush({
      //   url:'/testtwo',
      //   hideNavbar:true,
      //   params: encodeURI(`id:100`)
      // })
      nativeRouter.openTargetRouter({
        type: "microapp",
        uri: "com.gm.microapp.mine",
        path: encodeURI(`/testtwo`),
        hideNavbar: true,
      })
    },
  },
}
</script>

